  Josh's Custom CSS Reset
  https://www.joshwcomeau.com/css/custom-css-reset/
*/

*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
  padding: 0;
}

@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

p {
  text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

#root, #__next {
  isolation: isolate;
}

/*------- Custom Properties --------*/

:root {
    --side-nav-color: #7289da;
    --side-nav-icons-color: #04030f;
    --main-color: #ffffff;
    --card-color: #99aab5;
    --add-color: #00a878;
    --remove-color: #b10f2e;
}

body {
    background-color: var(--main-color);
    display: flex;
}

dialog {
    margin: auto;
    height: 40vh;
    width: 40vw;
    padding: 1.5rem;

    border: 3px solid var(--side-nav-color);
    background-color: var(--side-nav-icons-color);
    color: var(--main-color);
    font-size: 1.3rem;
}

dialog form{
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding: 10px;
}


dialog form select,
dialog form  input {
    outline: none;
    background-color: var(--card-color);
    color: var(--side-nav-icons-color);
    font-weight: 600;
    width: 100%;
}

dialog form  input {
    width: 100%;
}

#add-todo-dialog form button,
#removeProject form button,
#addProject form button {
    padding: 0.5rem 1rem;
    border: 2px solid var(--main-color);
    border-radius: 6px;
    cursor: pointer;

    display: flex;
    justify-content: center;
    align-items: center;
}

#add-todo-dialog div,
#removeProject div:last-of-type,
#addProject div:last-of-type {
    align-self: flex-end;
    margin-top: 1rem;

    display: flex;
    gap: 8px;
}

#add-todo-dialog {
    min-height: 70vh;
    min-width: 60vw;
}

#add-todo-dialog form button{
    background-color: var(--side-nav-icons-color);
    color: var(--main-color);
}

#add-todo-dialog textarea{
    resize: none;
}

.navbar {
    background-color: var(--side-nav-icons-color);
    color: var(--side-nav-color);
    min-height: 100vh;
    min-width: 10rem;

    display: flex;
    flex-direction: column;
    gap: 1rem;
}

.navbar > h2 {
    margin-top: 1rem;
    margin-left: 1rem;
}

.navbar > div {
    align-self: flex-end;
    margin-right: 1rem;
}

.navbar button {
    background: none;
	color: inherit;
	border: none;
    cursor: pointer;
}

.navbar ul{
    list-style-type: none;
    padding: 0;
    margin: 0;
}

.navbar ul li{
    text-decoration: none;
    color: var(--main-color);
    font-size: 1.4rem;
}

.navbar li:hover{
    background-color: var(--side-nav-color);
    color: var(--side-nav-icons-color);
}

.navbar ul li span{
    margin-left: 1rem;
    cursor: pointer;
}

main {
    flex-grow: 1;
}

button.add {
    color: var(--add-color);
    border: 1px solid var(--add-color);
    border-radius: 6px;
    margin-right: 5px;
}

button.add:hover {
    box-shadow: var(--add-color) 1.95px 1.95px 2.6px;
}

button.remove {
    color: var(--remove-color);
    border: 1px solid var(--remove-color);
    border-radius: 6px;
}

button.remove:hover {
    box-shadow: var(--remove-color) 1.95px 1.95px 2.6px;
}

main {
    padding: 2rem;
    display: flex;
    flex-direction: column;
}

.todos-container {
    margin-top: 3rem;
    flex-grow: 1;

    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-template-rows: auto;
    gap: 10px;
}

.todos-container article {
    position: relative;
    border: 2px solid var(--side-nav-color);
    border-radius: 8px;
    padding: 1rem;

    display: flex;
    flex-direction: column;
    gap: 5px;
}

.todos-container article:hover {
    top: -2px;
    box-shadow: #b147d17a 1.95px 1.95px 2.6px;
    box-shadow: rgba(0, 0, 0, 0.15) 1.95px 1.95px 2.6px;
}

.todos-container article > :nth-child(-n + 2) {
    border-bottom: 1px solid var(--side-nav-icons-color);
}

.todos-container article button {
    margin-top: auto;
    align-self: flex-end;
    background: none;
    border: none;
}

.current-project {
    background-color: var(--side-nav-color);
    font-weight: 700;
}

.current-project span {
    color: var(--side-nav-icons-color);
}

